
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

import {appUpdateSce} from '@/api/home';
import draggable from 'vuedraggable';

export default {
name: 'app',
components: {
Swiper, SwiperItem, GroupTitle, Grid, GridItem, draggable
},
data() {
return {
isHelpOpen: false,
isMenuOpen: false,
quickMenuList: [],
menuBgImg: {
backgroundImage: "url(" + require("../src/assets/images/whitebg.png") + ")",
backgroundRepeat: "no-repeat",
backgroundSize: "100% 100%",
tags: [],
list: [],
},
vuegConfig: {
forwardAnim: 'swing',  //options所有配置可以写在这个对象里，会覆盖全局的配置
disable: false              //对于嵌套路由，默认为关闭动画，需要在组件的data.vuegConfig中配置disable为false启用
},
isPull: false,//弹出悬浮窗
isMenuPull: false,
isShowButtom: "",
isPullPag: false,
isHidden: true,
}
},
methods: {
openMenu() {
this.isMenuOpen = true;
this.isPullPag = true;
window.onscroll = function () {
document.body.scrollTop = 0;
};
this.getappUpadte();
},

closeMenu() {
this.isMenuOpen = false;
this.isPullPag = false;
window.onscroll = function () {
document.body.scrollTop != 0;
};
},

goTarget(item) {
window.localStorage.setItem('workflowStatus', "1");
window.localStorage.setItem('applyStatus', "1");
let selectItem = item.menuResource.split("/#/")[1];
if (selectItem) {
this.$router.push('/' + selectItem);
} else {
this.$router.push('/' + item.menuResource);
}
this.isMenuOpen = false;
},

menuIndexBack() {
this.$router.push('/');
},
showFloatMenu() {//悬浮框
this.isPull = !this.isPull;
},
showRightFloatMenu() {//悬浮框
this.isMenuPull = !this.isMenuPull;
},
menuPersonal() {
this.$router.push('/personal');
this.isMenuOpen = false;
},
getappUpadte() {
appUpdateSce({"appEquipment": "Android"}).then(response => {
if (response.data && response.data) {
this.quickMenuList = response.data.menuCateories.categoryMenus[0].menus;
this.isLoadingData = false;
} else {
this.isLoadingData = false;
}
}).catch(error => {
this.isLoadingData = false;
})
},
},
mounted() {
this.getappUpadte();

window.addEventListener("popstate", function (e) {
this.closeMenu();
}.bind(this), false);

try {
window.ACallBackToken = function (message) {
var obj = JSON.parse(message);
window.localStorage.token = obj.token;
};
window.bCallBack = function (message) {
hmsPopup.showShortCenterToast(message);
};
var dict = {
"className": "BaseBridge",
"function": "getBaseInfo",
"successCallBack": "ACallBackToken",
"failureCallBack": "bCallBack"
};
HandBridge.postMessage(JSON.stringify(dict));
} catch (err) {

}
},
}

function getScript(src) {
document.write('<' + 'script src="' + src + '"' + ' type="text/javascript"><' + '/script>');
}

getScript('https://map.qq.com/api/js?v=2.exp&key=B4WBZ-JHB3F-QOVJT-JMXWF-LRVUS-TQFEB&libraries=drawing,geometry,autocomplete,convertor');





