import {fetch} from '@/utils/fetch'

/**
 * 获取首页轮播图
 */
export const getServiceHomePic = (params) => fetch({
    url: 'i/api/getServiceHomePic?showType=banner',
  method: 'get',
  data: params
});

/**
 * 获取首页轮应用
 */
export const appUpdateSce = (params) => fetch({
  url: 'i/api/appUpdateSce',
  method: 'post',
  data: params
});


/**
 * 对常见应用排序，添加，删除接口
 */
export const insertOrDelOrSort = (params) => fetch({
  url: 'i/api/insertOrDelOrSort',
  method: 'post',
  data: params
});




