

import Vue from 'vue'
import App from './App'
import router from './router'
import FastClick from 'fastclick';
import store from './store/store';
import './utils/filter';
import VueScroller from 'vue-scroller';
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'


Vue.use(VueScroller);
Vue.use(ToastPlugin);
Vue.use(LoadingPlugin);
Vue.use(AlertPlugin);

const options = {
duration: '0.3',              //转场动画时长，默认为0.3，单位秒
firstEntryDisable: false,     //值为true时禁用首次进入应用时的渐现动画，默认为false
firstEntryDuration: '.6',     //首次进入应用时的渐现动画时长，默认为.6
forwardAnim: 'pulse',   //前进动画，默认为fadeInRight
backAnim: 'fadeInLeft',       //后退动画，默认为fedeInLeft
sameDepthDisable: false,      //url深度相同时禁用动画，默认为false
tabs: [{
name: 'Home'
}, {
name: 'punchCard'
}],                       //默认为[]，name对应路由的name,以实现类似app中点击tab页面水平转场效果，如tab[1]到tab[0]，会使用backAnim动画，tab[1]到tab[2]，会使用forwardAnim动画
tabsDisable: false,           //值为true时，tabs间的转场没有动画，默认为false
disable: false,               //禁用转场动画，默认为false，嵌套路由默认为true
};

Vue.config.productionTip = false;
FastClick.attach(document.body);

if (window.location.protocol === 'file:' || window.location.port === '3000') {
let cordovaScript = document.createElement('script');
cordovaScript.setAttribute('type', 'text/javascript');
cordovaScript.setAttribute('src', 'cordova.js');
document.body.appendChild(cordovaScript)
}

/* eslint-disable no-new */
new Vue({
el: ' #app',
router,
template: '<App/>',
components: {App}
});



