//上下班转换
Vue.filter('onAndOffDuty', function (param) {
  switch (param) {
    case '工作开始时间':
      return '上班卡';
      break;
    case '工作结束时间':
      return '下班卡';
      break;
    default:
      break;
  }
});

//字符串截取时间（2017-09-17）里面的天 比如17
Vue.filter('getDay', function (param) {
  let schedulingDay = param.slice(8, 10);

  let date = new Date();
  let year = date.getFullYear();
  let month = date.getMonth() + 1;
  let strDate = date.getDate();
  if (month >= 1 && month <= 9) {
    month = "0" + month;
  }
  if (strDate >= 0 && strDate <= 9) {
    strDate = "0" + strDate;
  }
  let nowTime = year + "-" + month + "-" + strDate;
  if (param === nowTime) {
    return "今";
  } else {
    return schedulingDay;
  }
});


//休假类型转换
Vue.filter('switchType', function (param) {
  switch (param) {
    case 'vacationInfo':
      return '休假申请';
      break;
    case 'applyHoliday':
      return '销假申请';
      break;
    case 'applyOvertime':
      return '加班申请';
      break;
    case 'applySupplySign':
      return '补打卡申请';
      break;
    default:
      return param;
      break;
  }
});

